<template>
  <v-container>
    <h2>Account Settings</h2>
    <p>This is where you can update your password.</p>
    <v-row>
      <v-col cols="8" xs="8" md="4">
        <v-text-field v-model="email" disabled></v-text-field>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field
            v-model="password"
            :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
            :rules="[rules.required, passwordsRule]"
            :type="show1 ? 'text' : 'password'"
            label="Password"
            @click:append="show1 = !show1"
          ></v-text-field>

          <v-text-field
            v-model="confirmPassword"
            :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
            :rules="[rules.required, passwordsRule]"
            :type="show2 ? 'text' : 'password'"
            label="Confirm Password"
            @click:append="show2 = !show2"
          ></v-text-field>

          <v-btn color="primary">
            Update Password
          </v-btn>
        </v-form>
      </v-col>
      <v-col cols="6" xs="2" md="4"></v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    confirmPassword: '',
    email: 'registered@email.com',
    password: 'password',
    show1: false,
    show2: false,
    valid: true,
    rules: {
      required: (v) => !!v || 'This field is required.'
    }
  }),

  computed: {
    passwordsRule() {
      return () =>
        this.password === this.confirmPassword || 'Passwords must match'
    }
  }
}
</script>
