<template>
  <v-container>
    <br />
    <v-row justify="center">
      <v-col cols="6" xs="2" md="4"></v-col>
      <v-col cols="8" xs="8" md="4">
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field
            v-model="email"
            :rules="[rules.required, rules.mustBeEmail]"
            label="E-mail"
          ></v-text-field>

          <v-text-field
            v-model="password"
            :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
            :rules="[rules.required]"
            :type="show ? 'text' : 'password'"
            label="Password"
            @click:append="show = !show"
          ></v-text-field>

          <br />

          <v-btn @click="login()" color="primary">
            LOGIN
          </v-btn>
        </v-form>
      </v-col>
      <v-col cols="6" xs="2" md="4"></v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    email: '',
    password: '',
    show: false,
    valid: true,
    rules: {
      required: (v) => !!v || 'This field is required.',
      mustBeEmail: (v) => /.+@.+\..+/.test(v) || 'E-mail must be valid'
    }
  }),

  methods: {
    login() {
      const correctCredentials = {
        isLoggedIn: true,
        token: 123
      }

      this.$store.dispatch('setLoginUser', correctCredentials)
      localStorage.setItem('token', correctCredentials.token)
      this.$router.push('/dashboard')
    },

    validate() {
      this.$refs.form.validate()
    }
  }
}
</script>
